.reservations {
    min-height: calc(100vh - 18rem);
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .reservations h2 {
    padding: 1.5rem 2rem;
    color: #495e57;
    height: max-content;
  }
  
  .reservations form {
    padding: 0 2rem 2rem;
    display: grid;
    align-items: center;
    grid-template-columns: minmax(12rem, 1fr);
    gap: 1rem;
  }
  
  .reservations form button {
    width: 12rem;
    height: 2.5rem;
    margin: 1rem auto;
  }
  
  .form-field {
    display: flex;
    width: 12rem;
    flex-direction: column;
    gap: 0.25rem;
  }
  
  .form-field p {
    font-size: 1rem;
    color: red;
  }
  
  .select-wrapper {
    position: relative;
  }
  
  .select-wrapper::after {
    content: url("/src/assets/arrowDown.svg");
    font-weight: 600;
    height: 1.5rem;
    width: 1.5rem;
    top: 0.5rem;
    right: 0.625rem;
    position: absolute;
  }
  
  .reservations button:disabled,
  .reservations button[disabled] {
    background-color: #edefee;
    border-color: #edefee;
    color: #adafae;
    cursor: not-allowed;
  }
  
  .confirmed-reservation {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 1.75rem 0;
    min-height: calc(100vh - 6.25rem - 18rem);
  }
  
  .confirmed-reservation h2 {
    padding-top: 1.5rem;
  }
  
  .confirmed-reservation p {
    padding-top: 0.25rem;
  }
  
  @media (max-width: 491px) {
    .reservations form {
      grid-template-columns: repeat(1, 1fr);
    }
  
    .reservations form button {
      grid-column: 1 / span 1;
    }
  }
  